<template>
  <div id="earnMoney" :class="{hasHeader: $utils.showNavBar()}">
    <HeaderNav v-if="$utils.showNavBar()"></HeaderNav>
    <!-- 顶部图片 -->
    <div class="topImage">
      <div class="imgage iconSize">
        <img src="https://jjlmobile.oss-cn-shenzhen.aliyuncs.com/vsn1_1/juejinbao1.png" width="375" alt="顶部图片">
      </div>
    </div>
    <!-- 顶部文字介绍 -->
    <div class="topText">
      <!-- <div class="text f36">为什么掘金宝要和你共享奖励？</div> -->
      <div class="text f30">掘金宝是一个非盈利平台，用户聚合创造的资本市场市值财富用户共享，不花钱也能拿原始股。</div>
      <div class="text f30">互联网公司用户越多，资本市场市场价值越大</div>
      <div class="text f30">掘金宝用户阅读分享文章即可持续累积原始股，分享平台市值财富。</div>
    </div>
    <!-- 第一步 -->
    <div class="stepContent" :class='{active: showArr[0] === 1}'>
      <div class="stepTitle flex-row jusB" @click="setShow(0)">
        <span>第一步：分享文章，立即锁粉</span>
        <div class="bgIcon arrow"></div>
      </div>
      <div class="stepWrapper" ref="stepContent0" :style="showArr[0] === 1 ? `height:${heightArr[0]}` : ''">
        <div class='countHeight'>
          <div class="stepBox">
            <div class="stepText">
              <div class="stepInner">1、认真阅读文章15秒，自动得金币</div>
            </div>
            <div class="iconSize readImage">
              <img src="https://jjlmobile.oss-cn-shenzhen.aliyuncs.com/vsn1_1/juejinbao7.jpg" alt="图片1">
            </div>
            <div class="stepText">
              <div class="stepInner">2、分享任何文章，新用户阅读立即锁粉，赚掘金宝原始股很容易！</div>
            </div>
            <div class="iconSize readImage">
              <img src="https://jjlmobile.oss-cn-shenzhen.aliyuncs.com/vsn1_1/juejinbao8.jpg" alt="图片1">
            </div>
          </div>
        </div>
      </div>
    </div>
    <!-- 第二步 -->
    <div class="stepContent" :class='{active: showArr[1] === 1}'>
      <div class="stepTitle flex-row jusB" @click="setShow(1)">
        <span>第二步：签到、开宝箱</span>
        <div class="bgIcon arrow"></div>
      </div>
      <div class="stepWrapper" :style="showArr[1] === 1 ? `height:${heightArr[1]}` : ''" ref="stepContent1">
        <div class='countHeight'>
          <div class="stepBox">
            <div class="stepText">
              <div class="stepInner">【签到】掘金宝app每天都有福利送，进入"任务"页面，自动签到得金币</div>
            </div>
            <div class="iconSize readImage">
              <img src="https://jjlmobile.oss-cn-shenzhen.aliyuncs.com/vsn1_1/juejinbao3.png" alt="开宝箱未开图片">
            </div>
          </div>
          <div class="stepBox">
            <div class="stepText">
              <div class="stepInner">【开宝箱】进入"任务"页面，点击右下角宝箱（每3小时更新一次，金币随机发放，邀请越多宝箱金币越多），分享给好友还能再获得奖励</div>
            </div>
            <div class="iconSize readImage">
              <img src="https://jjlmobile.oss-cn-shenzhen.aliyuncs.com/vsn1_1/juejinbao2.png" alt="开宝箱图片">
            </div>
          </div>
        </div>
      </div>
    </div>
    <!-- 第三步 -->
    <div class="stepContent" :class='{active: showArr[2] === 1}'>
      <div class="stepTitle flex-row jusB" @click="setShow(2)">
        <span>第三步：邀请好友还能让你赚更多</span>
        <div class="bgIcon arrow"></div>
      </div>
      <div class="stepWrapper" :style="showArr[2] === 1 ? `height:${heightArr[2]}` : ''" ref="stepContent2">
        <div class='countHeight'>
          <div class="stepBox">
            <div class="stepText">
              <div class="stepInner">每邀请一个好友，您都可以获得丰厚奖励，邀请好友越多，奖励越丰厚！</div>
            </div>
            <div class="iconSize readImage">
              <img src="https://jjlmobile.oss-cn-shenzhen.aliyuncs.com/vsn1_1/juejinbao3.png" alt="任务">
            </div>
          </div>
        </div>
      </div>
    </div>
    <!-- 第四步 -->
    <div class="stepContent" :class='{active: showArr[3] === 1}'>
      <div class="stepTitle flex-row jusB" @click="setShow(3)">
        <span>第四步：兑换＆收益</span>
        <div class="bgIcon arrow"></div>
      </div>
      <div class="stepWrapper" :style="showArr[3] === 1 ? `height:${heightArr[3]}` : ''" ref="stepContent3">
        <div class="countHeight">
          <div class="stepBox">
            <div class="stepText">
              <div class="stepInner">① 消息中心通知您前一天收益情况，包括最新活动消息。</div>
              <div class="stepInner">② 可看到您当前的金币数目及原始股余额。</div>
              <div class="stepInner">③ 可查看最近的收益详情。</div>
            </div>
            <div class="iconSize readImage">
              <img src="https://jjlmobile.oss-cn-shenzhen.aliyuncs.com/vsn1_1/juejinbao4.png" alt="收益">
            </div>
          </div>
        </div>
        
      </div>
    </div>
    <!-- 我知道了按钮 -->
    <div class="getButton flex-row" @click="back">
      <div class="bag iconSize">
        <img src="../../../assets/img/bag.png" alt="bag">
      </div>
      <span>知道了 开始赚钱</span>
    </div>
  </div>
</template>
<script>
import HeaderNav from '@/components/task/header-nav'
export default {
  components: {
    HeaderNav
  },
  data () {
    return {
      showArr: [0, 0, 0, 0], // 所有的默认不展开
      heightArr: [] // 高度数组
    }
  },
  methods: {
    back () {
      this.$router.replace('/task')
    },
    // 设置显示隐藏
    setShow (index) {
      if (this.showArr[index] === 1) {
        this.$set(this.showArr, index, 0)
      } else {
        this.$set(this.showArr, index, 1)
      }
    },
    countHeight () {
      this.heightArr = []
      for (let i = 0; i <= 3; i++) {
        let el = this.$refs['stepContent' + i]
        this.heightArr.push(el.getElementsByClassName('countHeight')[0].clientHeight + 40 + 'px')
      }
    },
    // 计算高度做动画
    setHeight () {
      this.$nextTick(() => {
        this.countHeight()
      })
    }
  },
  mounted () {
    this.setHeight()
  }
}
</script>
<style lang="scss" scoped>
#earnMoney{
  height: 100%;
  box-sizing: border-box;
  font-size: 30px;
  color: #232323;
  .topImage{
    height: 370px;
    padding: 70px 0 50px;
  }
  .topText{
    line-height: 42px;
    padding: 0 50px 70px;
    .text{
      margin-bottom: 12px;
      line-height: 1.5;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
  // 步骤
  .stepContent{
    .stepTitle{
      padding: 0 50px;
      border-top: 1px solid #FEDD9A;
      border-bottom: 1px solid #FEDD9A;
      margin-top: -1px;
      background: #FFF5E0;
      height: 100px;
      line-height: 100px;
      color: #FFAA00;
      font-size: 32px;
      .arrow{
        width: 23px;
        height: 23px;
        background-image: url('../../../assets/img/task/arrow_bottom_yellow.png');
        background-size: 23px 12px;
        transition: all .3s;
        transform: rotate(-90deg);
      }
    }
    .stepWrapper{
      overflow: hidden;
      height: 0;
      transition: all .3s;
      .stepBox{
        width: 650px;
        margin: 40px auto;
        background: #F4F5F7;
        overflow: hidden;
        border-radius: 4px;
        .stepText{
          padding: 40px 0;
          .stepInner{
            margin-bottom: 10px;
            line-height: 42px;
            padding: 0 40px;
            text-align: justify;
          }
        }
        .stepInner:last-child{
          margin-bottom: 0;
        }
        .readImage{
          width: 580px;
          height: 1032px;
          margin: 0px auto;
          padding-bottom: 40px;
        }
      }
    }
    
  }
  .stepContent.active{
    .stepTitle{
      .arrow{
        background-image: url('../../../assets/img/task/arrow_bottom_yellow.png');
        transform: rotate(0deg);
      }
    }
  }
  .getButton{
    background: #F37F32;
    border-radius: 45px;
    width: 480px;
    height: 90px;
    margin: 50px auto 50px;
    color: #fff;
    .bag {
      width: 71px;
      height: 59px;
      margin-right: 40px;
    }
  }
}
</style>



